import React from 'react';
import styled from 'styled-components';
import { themeVal, glsp } from '@theme/utils';
import { ThugPugsThemeProvider } from '@theme/provider';
import { media } from '@theme/media';

import SEO from '../components/seo';

import Constrainer from '../styles/constrainer';
import { StaticImage } from 'gatsby-plugin-image';
import { Link } from 'gatsby';
import { Heading } from '../styles/heading';
import { Lead } from '../styles/type';

const Page = styled.div`
  background: ${themeVal('color.primary')};
  color: ${themeVal('color.base')};
  min-height: 100vh;
  display: flex;
`;

const PageConstrainer = styled(Constrainer)`
  display: flex;
  flex-flow: column;
  gap: ${glsp(2)};
`;

const PageHeader = styled.header`
  padding: ${glsp(1, 0)};

  ${media.largeUp`
    padding: ${glsp(3, 0)};
  `}
`;

const Main = styled.main`
  position: relative;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: auto 1fr;
  gap: ${glsp(2)};
  flex: 1;
  padding: ${glsp(1, 0)};

  ${Heading},
  ${Lead} {
    grid-column: 1 / span 5;
  }

  > .gatsby-image-wrapper {
    display: none;
  }

  ${media.smallUp`
    ${Heading},
    ${Lead} {
      grid-column: 1 / span 3;
    }
  `}

  ${media.mediumUp`
    grid-template-columns: repeat(6, 1fr);
    grid-template-rows: auto 1fr;
    gap: ${glsp(2)};

    ${Heading},
    ${Lead} {
      grid-column: 1 / span 4;
    }
  `}

  ${media.largeUp`
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: auto 1fr;
    gap: ${glsp(2)};

    ${Heading},
    ${Lead} {
      grid-column: 1 / span 5;
    }

    > .gatsby-image-wrapper {
      position: absolute;
      display: block;
      bottom: 0;
      right: 0;
    }
  `}
`;

const HomeLink = styled(Link)`
  text-decoration: none;

  &,
  &:visited {
    color: ${themeVal('color.surface')};
  }

  &:hover {
    color: ${themeVal('color.surface-400a')};
  }
`;

const UhOh = () => {
  return (
    <ThugPugsThemeProvider>
      {/* eslint-disable-next-line */}
      <SEO title='Not Found' />
      <Page>
        <PageConstrainer>
          <PageHeader>
            <Link to='/' aria-label='Thug Pugs'>
              <StaticImage
                src='../images/thug-pugs-logo.svg'
                alt='Thug Pugs logotype'
                placeholder='blurred'
                width={96}
                height={96}
              />
            </Link>
          </PageHeader>
          <Main>
            <Heading variation='base' size='xxlarge'>
              We lost this page
            </Heading>
            <Lead>
              We searched high and low but couldn&apos;t find what you&apos;re
              looking for. Let&apos;s find a better place for you to go.
              <br />
              Visit the <HomeLink to='/'>homepage</HomeLink>.
            </Lead>
            <StaticImage
              src='../images/pug-404.png'
              alt='Thug Pug'
              placeholder='blurred'
              width={600}
              height={600}
            />
          </Main>
        </PageConstrainer>
      </Page>
    </ThugPugsThemeProvider>
  );
};

export default UhOh;
